html, body {
  width: 100%;
  margin: 0;
  padding: 0;
}

body {
  background: rgb(53, 140, 226);
  color: white;
  font-family: "ZIlla Slab", serif;
  text-align: center;
}

h1, h2 {
  text-shadow: 1px 1px 0 black;
}

#root {
  margin: 0 auto;
}

a {
  color: inherit;
}

b {
  padding: 0 1em;
}

.demos-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.Demo {
  width: 600px;
  max-width: 100vw;
  margin: 2em;

  -webkit-animation: appear-top 500ms ease-out 0s 1 running forwards;

          animation: appear-top 500ms ease-out 0s 1 running forwards;
  opacity: 0;
}

.Demo:nth-child(1) { -webkit-animation-delay: 0s; animation-delay: 0s; }
.Demo:nth-child(2) { -webkit-animation-delay: 250ms; animation-delay: 250ms; }
.Demo:nth-child(3) { -webkit-animation-delay: 500ms; animation-delay: 500ms; }

.box {
  border-radius: 0.5em;
  background: rgba(255, 255, 255, 0.9);
  color: black;
  text-align: left;
  overflow: hidden;

  -webkit-transform: scale(1);

      -ms-transform: scale(1);

          transform: scale(1);
}


/* mouse follow demo */


.MouseFollower {
  min-height: 300px;
}

.MouseFollower > * {
  pointer-events: none;
}

.MouseFollower img {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}


/* carousel demo */

.Carousel {
  overflow-x: scroll;
}

.Carousel-layout {
  display: -ms-flexbox;
  display: flex;
}


/* side menu demo */

.SideMenu {
  position: relative;
}

.SideMenu-menu {
  position: absolute;
  width: 200px;
  left: -200px;

  -webkit-transition: left ease-out 150ms;

  -o-transition: left ease-out 150ms;

  transition: left ease-out 150ms;

  background: rgb(53, 140, 226);
}

button:focus + .SideMenu-menu {
  left: 0;
}

@-webkit-keyframes appear-top {
  from {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    opacity: 0;
  }

  50% {
    opacity: 0.25;
  }

  to {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    opacity: 1;
  }
}

@keyframes appear-top {
  from {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    opacity: 0;
  }

  50% {
    opacity: 0.25;
  }

  to {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    opacity: 1;
  }
}



